
.paginator {
    margin: 10px;
    margin-left: 23%;
}

.paginator button:disabled {
    background-color: #a7a7a7;
}

.pageNumber {
    float: left;
    width: 23px;
    height: 23px;
    text-align: center;
    padding: 2px;
    margin-right: 2px;
    margin-left: 2px;
    border: 1px solid #ffffff;
    border-radius: 20px;
    background-color: #ffe6a2;

}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: rgb(0, 0, 0);
}

.prevBut {
    height: 23px;
    float: left;
    margin-top: 4px;
    margin-right: 2px;
    border-radius: 10px;
    background-color: #9dc7e0;
    border: 1px solid white;
    color: white;
    outline: none;
}

.nextBut {
    alignment: right;
    height: 23px;
    margin-top: 4px;
    margin-left: 2px;
    border-radius: 10px;
    background-color: #9dc7e0;
    border: 1px solid white;
    color: white;
    outline: none;
}

